

.content{
 min-height:100vh;
 
 width: 100%;

}
.cont100{
    display: flex;
    height: 300px;
    width: 100%;
    justify-content: center;  
}
.cont99{
    background: url(https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png) center/cover no-repeat; 
    height: 300px;
    width: 300px;
    transform: scale(0.8);
    border-radius: 50%;
    
}
.cont99:hover{
    transform: scale(1.0);
    transition: 2s;
}
.cont98{
    padding: 30px 20px 20px 30px;
    height: 100vh;
    min-width: 200px;
    max-width: 100%;
    display: flex;
    position: relative;
    font-style: italic;
    color: rgb(252, 9, 90);
    

}
