
.main_h{
    display: flex;
    justify-content:center ;
    position: relative;
    top: 200px;

    left: 50px;
    
}
.h123{
    overflow: hidden;
    height: 100vh;
    width: 100%;
    background-image: url("https://images.pexels.com/photos/1024975/pexels-photo-1024975.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
    background-size: cover;
    background-position: center;
}
.text{
    color: aliceblue;
}
.heart{
    display: flex;
    transform: scale(1.5);
    position: relative;
    justify-content:center ;
  
}
.heart .span1{
    width: 80px;
    height: 120px;
    background: rgb(255, 0, 0); 
    border-top-left-radius:50px; 
    border-top-right-radius: 50px;
    transform: rotate(-45deg)
}
.heart .span2{
    width: 80px;
    height: 120px;
    background: rgb(255, 0, 0); 
    border-top-left-radius:50px; 
    border-top-right-radius: 50px;
    transform: rotate(45deg);
    position:relative;
    right: 50px;
}


.heart:hover{
    transform: scale(2);
    transition: 2s;
}
.text{
    text-align: center;
    text-decoration: dotted;
    font-size: xx-large;
    justify-content: center;
    position: relative;
    top: 120px;
   
}