.box0{
  display: flex;
  justify-content: center;
  height:100vh;
  max-width: 100%;
  background-image: url("https://images.pexels.com/photos/776635/pexels-photo-776635.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2");
  background-size: cover;
}
.box{
    display: flex;
    justify-content: center;
    height: 250px;
    width:250px;
    top: 200px;
    position: relative;
    
}
.input:hover{
  transform: scale(1.2);
  transition: 2s;

}
.passcont {
    margin-bottom: 20px;
    justify-content: center;
    text-align: center;
  }
.format_1{
  font-size: 0.7em;
  font-weight: 500;
  color: #ff0707;
  text-align: center;
  padding-bottom: 20px;
  font: sans-serif;
}

.text2{
  display: flex;
  position: relative;
  justify-content: center;
  top: 25px;
  left: 10px;
  padding: 3px;
  padding-top: 6px;
  font-size: 0.6em;
  font-weight: 100;
  height: 20px;
  width:150px;
  color: white;
  border-radius: 50px;
  background-color: rgb(255, 0, 0);
}
  .input {
    color: #ff0707;
    border: 2px solid #ff0707;
    border-radius: 10px;
    padding: 10px 25px;
    background: transparent;
    max-width: 230px;
    font-size: 10px;
   }
   .input:active {
    box-shadow: 2px 2px 15px #ff0707 inset;
   }
  
  .validate-button {
    display: flex;
    color: #ff0707;
    border: 2px solid #ff0707;
    position: relative;
    top: 50px;
    left: 40px;
    justify-content: center;
    padding: 5px 20px;
    text-align: center;
    border-radius: 50px;
  }
  .validate-button:hover{
    color: white;
    background-color: #ff0707;
  }